@import 'vars';


@mixin mobile {
  @media (max-width: #{$mobile-width}) {
    @content; } }

@mixin small-mobile {
  @media (max-width: #{$small-mobile-width}) {
    @content; } }

@mixin non-mobile {
  @media (min-width: #{$non-mobile-width}) {
    @content; } }



@mixin desktop {
  @media (min-width: #{$desktop-width}) {
    @content; } }

@mixin large-desktop {
  @media (min-width: #{$large-desktop-width}) {
    @content; } }
