@import 'vars';
@import 'mixins';

.#{$prefix}user {
  display: flex;
  color: $dark-grey;

  &-location {
    font-size: 10px;
    margin: 5px 0 0 0; }

  &-name {
    margin: 0; } }

.#{$prefix}sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: $light;
  width: $sidebar-width;
  box-sizing: border-box;
  border-right: 4px solid #ddd;
  border-top: 1px solid #ddd;

  @include mobile {
    display: none; }

  &__user {
    background: #fff;
    padding: 25px 15px; }

  &__item {
    &--active {

      & > .#{$prefix}link--sidebar {
        background: #fff;
        color: $dark-grey;
        font-weight: 500;
        position: relative;
        &:after {
          content: "";
          border: solid #808080;
          border-width: 0 2px 2px 0;
          display: inline-block;
          padding: 2px;
          position: absolute;
          right: 6px;
          top: 19px;
          border-radius: 1px;
          transform: rotate(-45deg); } } }


    &:last-child {
      .#{$prefix}link--sidebar {
        border-bottom: none; } } } }
